import { Injectable } from '@angular/core';
import { forEach, join } from 'lodash';
import { CsvData } from './csv.types';

@Injectable({
  providedIn: 'root'
})
export class RxCsvService {
  convertToCsv(csvData: CsvData): string {
    let csv = '';

    forEach(csvData, (row) => {
      const record = join(row, ',');

      csv += `${record}\n`;
    });

    return csv;
  }
}
